@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap");
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Raleway", sans-serif;
  background-color: var(--background);
  color: var(--foreground);
}
body > header {
  width: 100%;
}
body > #site-content {
  margin-top: 3.5rem;
  padding: 15px;
  flex: 1;
  max-width: 960px;
}
body > footer {
  width: 100%;
  height: 60px;
  display: flex;
}

.o-grid {
  display: grid;
  width: 100%;
  gap: 0.25rem;
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 570px) {
  .o-grid {
    display: flex;
    flex-direction: column;
  }
}
.o-grid.two-columns {
  grid-template-columns: 1fr 1fr;
}
.o-grid.three-columns {
  grid-template-columns: 1fr 1fr 1fr;
}

.active-border {
  border: 1px solid var(--foreground-25);
}
.active-border:hover, .active-border:hover ~ .counter {
  border-color: var(--foreground-50);
}
.active-border:focus, .active-border:focus ~ .counter {
  border-color: var(--accent);
}
.active-border ~ .counter {
  border: 1px solid var(--foreground-25);
  border-top: none;
}

.mt-3 {
  margin-top: 1rem;
}

.monospace {
  font-family: "Courier New", Courier, monospace;
}

h1, h2, h3, h4, h5 {
  line-height: 1.2;
}

h1 {
  font-size: 2.2rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.8rem;
}

h4 {
  font-size: 1.6rem;
}

h5 {
  font-size: 1.4rem;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class],
*::before,
*::after {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd,
*::before,
*::after {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

ul[class],
ol[class] {
  list-style: none;
}

a:not([class]) {
  color: var(--accent);
  text-decoration-skip-ink: auto;
}
a:not([class]):hover {
  text-decoration: underline;
}

a[class] {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
nav#top-nav {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3.5rem;
  z-index: 99999;
  padding: 0.25rem 4rem;
  background-color: var(--background);
  border-bottom: 1px solid var(--foreground-50);
  transition: height 0.2s ease-in-out;
}
@media screen and (max-width: 570px) {
  nav#top-nav {
    padding: 0.1rem 1rem;
  }
}
@media screen and (max-width: 445px) {
  nav#top-nav {
    flex-direction: column;
    padding: 0 0.5rem;
    height: auto;
  }
}
nav#top-nav.bottom {
  bottom: 0;
}
nav#top-nav.compact {
  height: max(2.275rem, 30px);
}
@media (prefers-reduced-motion) {
  nav#top-nav.compact {
    height: 3.5rem;
  }
}
nav#top-nav .navbar-nav {
  display: flex;
  flex-direction: row;
}
nav#top-nav .navbar-nav.left {
  margin-right: auto;
}
nav#top-nav .navbar-nav > .nav-item {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--foreground-75);
}
nav#top-nav .navbar-nav > .nav-item:hover {
  color: var(--foreground);
  background-color: var(--accent-10);
}
nav#top-nav .navbar-nav > .nav-item.register {
  font-weight: 600;
  background-color: var(--accent-25);
  border: 1px solid var(--accent-50);
  border-radius: 0.25rem;
}
nav#top-nav .navbar-nav > .nav-item.register:hover {
  background-color: var(--accent-50);
  border-color: var(--accent-75);
}
nav#top-nav .navbar-nav > .nav-item.navbar-brand {
  color: var(--foreground-75);
  margin-right: 1rem;
  font-size: 1.25rem;
  line-height: inherit;
  white-space: nowrap;
}
nav#top-nav .navbar-nav > .nav-item.navbar-brand:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 3px var(--accent-25);
}
nav#top-nav .navbar-nav > .nav-item > .nav-link {
  padding: 0 0.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  color: var(--foreground);
}
nav#top-nav .navbar-nav > .nav-item > .nav-link:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 3px var(--accent-25);
}
nav#top-nav .navbar-nav > .nav-item > .nav-link.light {
  color: var(--foreground-75);
}
nav#top-nav .navbar-nav > .nav-item > .nav-link.user > .name {
  color: var(--foreground-75);
  padding-left: 0.5ch;
}
@media screen and (max-width: 570px) {
  nav#top-nav .navbar-nav > .nav-item > .nav-link.user > .name {
    display: none;
  }
}

#sub-nav {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 1rem 0;
  margin-bottom: 2rem;
  border-bottom: 1px solid var(--foreground-50);
}
#sub-nav > .nav-link {
  position: relative;
  display: flex;
  margin: 0 0.5rem;
  padding: 0.5rem 1rem;
  flex-direction: column;
  align-items: center;
  border: 1px solid var(--foreground-25);
}
#sub-nav > .nav-link:hover {
  border-color: var(--foreground-50);
}
#sub-nav > .nav-link:focus {
  border-color: var(--accent);
}
#sub-nav > .nav-link:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 3px var(--accent-25);
}
#sub-nav > .nav-link:hover {
  background-color: var(--accent-10);
}
#sub-nav > .nav-link.active:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  left: 0;
  bottom: 100%;
  background-color: var(--accent);
  border-radius: 5px 5px 0 0;
}
#sub-nav > .nav-link > .material-icons-outlined {
  font-size: 2rem;
  color: var(--foreground-75);
}
#sub-nav > .nav-link > .name {
  font-size: 1.2rem;
  color: var(--foreground-75);
}

footer.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid var(--foreground-50);
  width: 100%;
  height: auto;
}
footer.footer > .cols {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  padding: 0.5rem;
}
@media screen and (max-width: 570px) {
  footer.footer > .cols {
    flex-direction: column;
  }
}
footer.footer > .cols > .col {
  display: flex;
  flex-direction: column;
  flex: 0 15%;
  padding: 0 0.5rem;
  border-right: 1px solid var(--foreground-25);
}
footer.footer > .cols > .col:last-child {
  border-right: none;
}
footer.footer > .cols > .col > .header {
  white-space: nowrap;
  font-weight: 500;
  padding: 0 0.25rem;
  border-bottom: 1px solid var(--foreground-25);
}
footer.footer > .cols > .col > .item {
  width: 100%;
  white-space: nowrap;
  padding: 0.25rem;
  color: var(--foreground-75);
}
footer.footer > .cols > .col > .item.follow {
  display: flex;
}
footer.footer > .cols > .col > .item.follow img {
  object-fit: contain;
  height: 1.5rem;
  margin-right: 1ch;
}
footer.footer > .cols > .col > span .hl {
  color: var(--foreground);
}
footer.footer > .cols > .col > a {
  color: var(--foreground);
  text-decoration: none;
}
footer.footer > .cols > .col > a:hover {
  text-decoration: none;
  background-color: var(--accent-10);
}
footer.footer > .cols > .col > button {
  border: none;
  background-color: transparent;
  text-align: left;
}
footer.footer > .cols > .col > button:hover {
  background-color: var(--accent-10);
}
footer.footer > .bottom {
  display: flex;
  width: 100%;
  justify-content: center;
  align-content: center;
  padding: 0.5rem;
  border-top: 1px solid var(--foreground-25);
}
footer.footer > .bottom > .copy-text {
  margin-right: 1ch;
}

.tag {
  display: grid;
  grid-template-areas: "main";
  line-height: 1;
  border: 1px solid var(--foreground-75);
  background-color: var(--background);
  color: var(--foreground);
  margin: 4px;
  cursor: pointer;
}
.tag:first-child {
  margin-left: 0;
}
.tag:hover {
  opacity: 0.9;
  color: var(--foreground);
}
.tag > .bg {
  grid-area: main;
  left: 0;
  top: 0;
  opacity: 0.5;
}
.tag > .name {
  grid-area: main;
  justify-self: center;
  align-self: center;
  margin: 0.1rem 0.2rem;
  z-index: 1;
}

img.avatar {
  display: inline;
}

.story-card {
  width: 100%;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: 10rem 1fr;
  grid-template-rows: auto auto 1fr auto auto;
  grid-column-gap: 0.25rem;
  margin: 0.75rem 0 0;
  padding: 0.25rem;
  border: 1px solid var(--foreground-25);
}
.story-card:hover {
  border-color: var(--foreground-50);
}
.story-card:focus {
  border-color: var(--accent);
}
.story-card:first-of-type {
  margin: 0;
}
.story-card:hover .info {
  border-color: var(--foreground-50);
}
@media only screen and (max-width: 570px) {
  .story-card {
    grid-template-columns: 6rem 1fr;
    flex-direction: column;
  }
}
.story-card .image {
  object-fit: contain;
  grid-row: 1/5;
  grid-column: 1;
  align-self: baseline;
  height: auto;
}
@media only screen and (max-width: 570px) {
  .story-card .image {
    grid-row: 2/4;
  }
}
.story-card .title {
  grid-column: 1/3;
  grid-row: 1;
  display: flex;
  align-items: baseline;
  margin: 0 4px;
  font-size: 2rem;
  line-height: 2rem;
  color: var(--foreground);
  overflow: hidden;
  text-overflow: ellipsis;
}
.story-card .title:hover {
  color: var(--foreground-75);
  text-decoration: underline;
  text-decoration-skip-ink: auto;
}
.story-card .title > h3 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline;
}
.story-card .title > .rating {
  display: inline;
  height: 1.4rem;
  width: 1.4rem;
  margin-right: 0.5rem;
}
.story-card > .author {
  grid-column: 1/3;
  grid-row: 2;
  margin: 0 4px;
  font-size: 1.1rem;
}
.story-card > .author > a {
  color: var(--foreground);
}
.story-card .hook {
  grid-column: 1/3;
  grid-row: 3;
  margin: 2px 4px;
  flex: 1;
}
.story-card .tags {
  grid-column: 1/3;
  grid-row: 4;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.story-card .image ~ .title {
  grid-column: 2;
}
@media only screen and (max-width: 570px) {
  .story-card .image ~ .title {
    grid-column: 1/3;
  }
}
.story-card .image ~ .author {
  grid-column: 2;
}
.story-card .image ~ .hook {
  grid-column: 2;
}
.story-card .image ~ .tags {
  grid-column: 2;
}
@media only screen and (max-width: 570px) {
  .story-card .image ~ .tags {
    grid-column: 1/3;
  }
}
.story-card .info {
  grid-column: 1/3;
  grid-row: 5;
  padding: 0.25rem;
  display: flex;
  border-top: 1px solid var(--foreground-25);
}
.story-card .info > * {
  padding: 0 0.25rem;
  color: var(--foreground-75);
  cursor: default;
}
.story-card .info > *:hover {
  color: var(--foreground);
}
.story-card .info .icon {
  font-size: 1rem;
}
.story-card .info .not-published {
  color: var(--red);
  margin-left: auto;
}

#story-app > .story-details {
  overflow-y: auto;
}
#story-app > .story-details > .titlebar {
  display: flex;
  align-items: baseline;
  flex-direction: row;
  padding-bottom: 0.5rem;
}
#story-app > .story-details > .titlebar > .rating {
  height: 1.4rem;
  width: 1.4rem;
  margin-right: 0.5rem;
}
#story-app > .story-details > .titlebar > h1.title {
  font-size: 2rem;
  line-height: 3rem;
}
#story-app > .story-details > .tags {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
#story-app > .story-details > .main {
  padding-top: 0.5rem;
  text-align: justify;
}
#story-app > .story-details > .main > .cover {
  float: left;
  margin-right: 10px;
  margin-bottom: 10px;
  width: 10rem;
  object-fit: contain;
  border: 1px solid var(--foreground-25);
}
#story-app > .story-details > .main h2.author {
  color: var(--foreground);
  font-weight: 500;
  font-size: 1rem;
}
#story-app > .story-details > .actions {
  display: flex;
  margin-bottom: 0.5rem;
}
#story-app > .story-details > .actions button {
  display: flex;
  align-items: center;
  background: transparent;
  border: 1px solid var(--foreground-25);
  color: var(--foreground);
  min-height: 2rem;
  margin-right: 0.1rem;
}
#story-app > .story-details > .actions button:hover {
  border-color: var(--foreground-75);
}
#story-app > .story-details > .actions .spacer {
  flex: 1;
}
#story-app > .chapters {
  margin-top: 1rem;
}
#story-app > .chapters > span {
  font-weight: bold;
}
#story-app > .chapters > .chapter {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  color: var(--foreground);
  border-top: 1px solid var(--foreground-25);
}
#story-app > .chapters > .chapter:last-child {
  border-bottom: 1px solid var(--foreground-25);
}
#story-app > .chapters > .chapter:hover {
  background-color: var(--accent-5);
}
#story-app > .chapters > .chapter > .left {
  display: flex;
  align-items: center;
}
#story-app > .chapters > .chapter > .left > * {
  padding: 0 0.25rem;
}
#story-app > .chapters > .chapter > .left > .title {
  color: var(--foreground);
}
#story-app > .chapters > .chapter > .left > .title:hover {
  color: var(--accent);
  text-decoration: underline;
}
#story-app > .chapters > .chapter > .left > .date {
  font-size: 0.8rem;
  color: var(--foreground-75);
}
#story-app > .chapters > .chapter > .left > .date:hover {
  color: var(--foreground);
}
#story-app > .chapters > .chapter > .left > .read-status {
  padding: 0;
  width: 1.2rem;
  height: 1.2rem;
  background-color: transparent;
  border: 1px solid var(--foreground-25);
}
#story-app > .chapters > .chapter > .left > .read-status:hover {
  border-color: var(--foreground-50);
}
#story-app > .chapters > .chapter > .left > .read-status:focus {
  border-color: var(--accent);
}
#story-app > .chapters > .chapter > .left > .read-status.active {
  display: flex;
  align-items: center;
  justify-content: center;
}
#story-app > .chapters > .chapter > .left > .read-status.active:after {
  display: block;
  content: "";
  width: 80%;
  height: 80%;
  background-color: var(--green-25);
  border: 1px solid var(--foreground-25);
}
#story-app > .info {
  border-top: 1px solid var(--foreground-25);
  margin-top: 1rem;
}
#story-app > .info > * {
  padding: 0 0.25rem;
  color: var(--foreground-75);
}
#story-app > .info > *:hover {
  color: var(--foreground);
}

.chapter-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chapter-details {
  display: flex;
  flex-direction: column;
  align-content: center;
}
.chapter-details > a.story {
  margin: auto;
  position: relative;
  text-align: center;
  font-style: italic;
  color: var(--foreground-75);
  text-decoration: underline;
}
.chapter-details > a.story:hover:before {
  content: "";
  display: block;
  position: absolute;
  width: 0.5rem;
  height: 0.5rem;
  transform: rotate(135deg);
  border: solid black;
  border-width: 0 1px 1px 0;
  bottom: 0.4rem;
  left: -0.5rem;
}
.chapter-details > h1.title {
  text-align: center;
  margin: 0.5rem;
}
.chapter-details > time.date {
  text-align: center;
  margin: 0;
}
.chapter-details .actions {
  display: flex;
  justify-content: center;
}
.chapter-details > .body-container > .notes {
  text-align: justify;
  font-style: italic;
  padding: 0.25rem 0.5rem;
  margin: 0.5rem 0;
  color: var(--foreground-75);
  border: 1px solid var(--foreground-25);
}
.chapter-details > .body-container > .notes > .an {
  font-weight: lighter;
}
.chapter-details > .body-container > .body {
  text-align: justify;
}

ul.my-list {
  margin: 0.5rem 0;
}
ul.my-list > li.item {
  display: flex;
  margin: 4px 0 0;
}
ul.my-list > li.item:first-of-type {
  margin: 0;
}

.comments-container {
  margin-top: 2rem;
  width: 70%;
}
@media screen and (max-width: 570px) {
  .comments-container {
    width: 100%;
  }
}
.comments-container > form {
  display: flex;
  flex-direction: column;
}
.comments-container > form > .comment-box {
  padding: 0.5rem;
  min-height: 5rem;
  background-color: var(--background);
  color: var(--foreground);
}
.comments-container > form > .buttons {
  display: flex;
}
.comments-container > form .comment-btn {
  flex: 1;
  background-color: var(--background);
  color: var(--foreground);
  margin: 1px 1px 0 0;
}
.comments-container > form .comment-btn > i.material-icons-outlined {
  font-size: inherit !important;
  vertical-align: middle;
}
.comments-container > form .help-btn {
  flex: 0 0 2rem;
  margin: 1px 0 0 0;
  text-align: center;
  color: var(--foreground);
}
.comments-container > form .help-btn > i.material-icons-outlined {
  vertical-align: middle;
}
.comments-container > .comments {
  padding-top: 0.5rem;
}

.comment {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  padding: 0.5rem 0;
}
.comment:hover .author, .comment:hover .main {
  border-color: var(--foreground-75);
}
.comment.marked {
  background: transparent linear-gradient(90deg, transparent 0%, var(--accent-25) 40%, var(--accent-25) 60%, transparent 100%);
  transition: background-color 100ms ease-in-out;
}
.comment.marked:hover {
  background: var(--accent-10) linear-gradient(90deg, transparent 0%, var(--accent-25) 40%, var(--accent-25) 60%, transparent 100%);
}
.comment > .author {
  display: flex;
  flex-direction: column;
  flex: 0 0 6rem;
  border: 1px solid var(--foreground-50);
  margin-right: 0.5rem;
  min-height: 100px;
  background-color: var(--background);
}
@media screen and (max-width: 570px) {
  .comment > .author {
    flex: 0 0 4rem;
    min-height: 75px;
  }
}
.comment > .author > .name {
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 600;
  padding: 0 0.2rem;
  color: var(--foreground);
}
.comment > .author > .avatar {
  border-top: 1px solid var(--foreground-50);
}
.comment > .author > .role-tag {
  margin: 0;
  border: none;
  border-top: 1px solid var(--foreground-50);
}
.comment > .main {
  flex: 1;
  border: 1px solid var(--foreground-50);
  background-color: var(--background);
}
.comment > .main > .header {
  display: flex;
  align-items: center;
  padding-left: 0.5rem;
  color: var(--foreground-50);
}
.comment > .main > .header > *:hover {
  color: var(--foreground-75);
}
.comment > .main > .header > .link {
  color: var(--foreground-50);
}
.comment > .main > .header > .actions {
  display: flex;
  flex-direction: row;
  margin-left: auto;
  padding: 0.1rem;
  opacity: 0.5;
}
.comment > .main > .header > .actions:hover {
  opacity: 1;
}
.comment > .main > .body {
  white-space: pre-line;
  margin: 0.5rem;
}
.comment > .main > .edit-time {
  font-size: 0.75rem;
  color: var(--foreground-50);
  margin: 0.5rem;
}
.comment > .main > .edit-time:hover {
  color: var(--foreground-75);
}

p.sm-line {
  display: inline-block;
  width: 1px;
  height: 0.8rem;
  background-color: var(--foreground-50);
  margin: 0 0.5rem;
}

.votes {
